import React, {useState, useEffect} from 'react';
import axios from 'axios';
import { IPhotos } from './interfacePhotos';
import http from '../../http';



const PhotosGet = ({userId = 0}: {userId?: number}) => {
    const [photos, setPhotos] = useState<IPhotos[]>([]);


    useEffect(() => { getPhotos()}, []);
    const getPhotos = async () => {
        // ASYNC AWAIT
        try {
            const responseData = await http.get(userId === 0 ? `/photos` : `/photos?albumId=${userId}`);
                setPhotos(responseData.data); 
            
        } catch (err) {
            alert(err);
        }
    };
    return (
        <>

            
            {userId === 0 && <button className="btn btn-primary ms-5 w-25" onClick={() => getPhotos()}>get photos</button>}
            <div>
                {photos.map(photo => <img src={`${photo.url}`} key={`${photo.id}`} className="img-fluid" alt={`${photo.title}`}/>)}
            </div>

            
        </>
    );
};

export default PhotosGet;