import React, { useContext, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import Context from '../../context/context';
import Login from '../Login/Login';

const Navbar = () => {
    const { isLogin, setIsLogin } = useContext(Context);
    const [isShow, setIsShow] = useState(false)
    const currenLocation = useLocation();
   /*  console.log(currenLocation); */

    const routes = [{
        path: '/users',
        name: 'Users'
    },
    {
        path: '/posts',
        name: 'Posts'
    },
    
/*     {
        path: '/albums',
        name: 'Albums'
    }, */
    {
        path: '/photos',
        name: 'Photos'
    },
/*     {
        path: '/todos',
        name: 'Todos'
    }, */
    {
        path: '/other',
        name: 'Other'
    },
    
    

    ]

    const logout = () => {
        localStorage.clear();
        setIsLogin(false);
    }

    return (
            <nav className="navbar navbar-expand-lg bg-dark navbar-dark">
                <div className="container-fluid">
                    <span className="navbar-brand ${isShow && 'show'}">Navbar</span>
                    <div>
                        <button className="navbar-toggler float-start" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" 
                        aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                        <span className="navbar-toggler-icon" onClick={() => setIsShow(!isShow)}></span>
                    </button>
                    </div>
                    {
                    isLogin ?
                    <>
                    <div className={`collapse navbar-collapse ${isShow && 'show'}`} id="navbarNav">
                        <ul className="navbar-nav">
                    
                        {
                            routes.map(route =>  
                                <li className="nav-item" key={route.path}>
                                    <Link className={`nav-link ${route.path === currenLocation.pathname && 'active'}`} to={route.path}>{route.name}</Link>
                                </li>)
                        }

                        
                        </ul>
                    </div>
               
               {/*  <Login/>  */}
                    <button 
                        className='btn btn-outline-secondary' 
                        type="submit"
                        onClick={logout}
                        >
                            LogOut
                    </button>
                  </>:
                  <Link to='login' className='btn btn-primary'>Login</Link>
                    }
                   
                </div>
    </nav>
    );
};

export default Navbar;